.App {
  text-align: center;
}

.header{
	margin-top: 10px;
	margin-bottom: 30px;
	justifyContent: flex-end;
	float: right;
	right: 20px;
}

.terminal {
	float:right;
	transform: rotate(-1deg);
	font-family: 'Segoe Ui Semibold';
	font-size: 30px;
}

.squads {
	display: flex;
	transform: rotate(-1deg);
	font-family: 'Segoe Ui Semibold';
	font-size: 30px;
}

@media all {
.page-break { display: none; }
}

@media print {
.page-break { display: block; page-break-before: always; }
}